.textBox {
    padding: 10px; // Padding for spacing
    border: 1px solid var(--app-text-white-color);
    border-radius: 8px; // Rounded corners
    transition: border-color 0.3s; // Smooth transition for border color
    color: var(--app-text-white-color);
    background-color: var(--colors-gray-dark-800);
    // Optional: Add a background color
    cursor: text; 
    &:focus {
        outline: none; // Remove default outline
    }

    &:disabled {
        cursor: not-allowed; // Not-allowed cursor style
    }
    &::-webkit-outer-spin-button, // Chrome, Safari, Edge
    &::-webkit-inner-spin-button { // Chrome, Safari, Edge
        -webkit-appearance: none; // Disable default appearance
        margin: 0; // Remove margins
    }
}
