.outerLandingPage {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
}

.titleBlock {
    width: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;

}

.title {
    font-weight: bold;
    font-size: 4rem;
    color: var(--app-text-white-color);
    margin-bottom: 1vh;

}

.desc {
    font-weight: bold;
    font-size: large;
    color: var(--app-text-white-color);
    margin-bottom: 2vh;

}

.subTitle {
    font-size: x-large;
    font-weight: bold;
    color: var(--app-green-color);
    margin-bottom: 2vh;
}

.gameSearchBox {
    margin-top: 2vh;
    width: 100%;
}

.getStartedBtn {
    background-color: var(--app-green-color);
    width: fit-content;
    border: none;

    &:hover {
        opacity: 0.9;
    }
}

.gamesListOuter {
    min-height: 100vh;
    padding: 5vh;
}

.inputBlock {
    display: flex;
    align-items: center;
    justify-content: center;
}

.searchInp {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 75%;
    background-color: transparent;
}

.gameCompOuter {
    cursor: pointer;
    border-radius: 10px;
    width: fit-content;
    width: 26vw;
    border: 1px solid var(--colors-gray-dark-700);

    &:hover {
        border: 1px solid var(--colors-gray-dark-1000);
        .launchGameBtn{
            color: var(--app-text-white-color);
            border: 1px solid var(--app-text-white-color);
        }
    }
}

.iconBlock {
    padding: 3vh 3vw  0vh 3vw;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1vw;
    margin-bottom: 2vh;
}

.sep {
    height: 1px;
    margin-bottom: 2vh;

    background-color: var(--app-text-white-color);
}

.aboutGameBlock {
    padding: 0vh 3vw 0vh 3vw

}

.gameTitle {
    color: var(--app-text-white-color);
    margin-bottom: 1.5vh;
    font-size: xx-large;
    font-weight: bold;
}

.gameDesc {
    color: var(--colors-gray-dark-1000);
    margin-bottom: 1.5vh;

}

.launchGameBtn {

    color: var(--colors-gray-dark-1000);
    padding: 3%;
    border: 1px solid var(--colors-gray-dark-1000);
    border-radius: 10px;
    width: fit-content;
}

.launchGameBtnOuter{
    padding: 0vh 0vw 3vh 3vw;

}

.noGameFound{
    border: 1px solid var(--app-text-white-color);
    color: var(--app-text-white-color);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: larger;
    padding: 3vh;
    width: 80%;
    height: 30vh;
}
.noGameFoundOuter{
    display: flex;
    align-items: center;
    justify-content: center;    
}

.gridContainer {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(26vw, 1fr)); /* Adjust min size */
    gap: 2vh; /* Space between items */
    padding: 2vh; /* Optional: Padding around the grid */
}
